* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}


html,
body {
background: rgb(12,74,213);
background: linear-gradient(180deg, rgba(12,74,213,1) 10%, rgba(6,60,181,1) 14%, rgba(9,9,125,1) 24%, rgba(2,0,36,1) 100%);
background: url(/AnimeList/static/media/fondo.90150f48.jpg) no-repeat center center/cover;
font-family: Arial, Helvetica, sans-serif;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background-color: white;
}

::-webkit-scrollbar-thumb {
  background-color:rgb(6, 17, 175);
  border-radius: 5px;
}

a {
  text-decoration: none;
}

.container {
  max-width: 1100px;
  margin: auto;
  padding: 0 20px;
}

.btn {
  display: inline-block;
  color: #fff;
  background-color: #2523a7;
  font-size: 1em;
  text-align: center;
  padding: 10px 15px;
  border: 0;
  margin: 10px 0;
}

header {
  padding-top:2% ;
  height: 15%;
}

header img {
  width: 10%;
}

.center {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-bottom: 5%;
}
.tit {
  justify-content: center;
  align-items: center;
  text-align: center;
  color: white;
  padding-left: 2%;
  font-family: 'Great Vibes', cursive;
  font-size: 2em;
}




.cards {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 1rem;
  gap: 1rem;
}


.flip-card {
  background-color: transparent;
  cursor: pointer;  
  height: 20em;
  perspective: 1000px;
}

.flip-card h1 {
  font-size: 1.6em;
  border-bottom: .1em rgb(255, 255, 255) solid;
  padding-bottom: 3%;
  margin-bottom: 5%;
}

.flip-card img {
  width: 100%;
  height: 20em;
  object-fit: cover;
}

.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
}

.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
}

.flip-card-front {
  background-color: rgb(255, 255, 255);
  color: black;
}

.flip-card-back {
  background-color: dodgerblue;
  color: white;
  padding: 7%;
  transform: rotateY(180deg);
}

li {
  list-style: none;
  padding-bottom: 5%;
}

.descrip{
  text-align: justify;
}

.h1T{
  font-size: 3em;
  border-bottom: .1em rgb(0, 0, 0) solid;
  padding-top: 1%;
  padding-bottom: 1%;
  padding-right: 4%;
  text-align: center;
  font-family: "Potta One";
  font-weight: bold;
  font-style: italic;
}

.search {
  margin-bottom: 5%;
  width: 50%;
  display: inline;
  margin-left: auto;
  margin-right: auto;
}
.boton{
  float: right;
  margin-right: 5%;
}

.footer{
  text-align: end;
  padding: 3px;
}

.modalT{
  background-color:rgba(255,255,255,0.5);
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
}


@media (max-width: 800px) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
  .flip-card {
    width: 100%;
    height: 25em;
    margin: 0;
  }
  .flip-card img {
    width: 100%;
    height: 100%;
    margin: 0;
  }
}


@media (max-width: 500px) {
  body{
    overflow-y: scroll;
    justify-content: center;
  }
  .cards {
    grid-template-columns: 1fr;
  }
  .flip-card {
    width: 100%;
    height: 25em;
    margin: 0;
  }
  .flip-card img {
    width: 100%;
    height: 100%;
    margin: 0;
  }
}
